import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { ChevronLeft } from 'react-feather'
import { Row, Col, Form, FormGroup, Label, Input, CustomInput, Button } from 'reactstrap'
import SubTitle from '../../components/SubTitle'
import firebase from 'firebase/app'
import './ForgotPassword.scss'

const ForgotPassword = () => {
  const [email, setEmail] = useState("")
  const [isSending, setSending] = useState(false)

  const handleSubmit = e => {
    e.preventDefault()
    setSending(true)

    firebase.auth()
      .sendPasswordResetEmail(email)
      .then(() => {
        setSending(false)
        alert('An email has been sent to you!')
      })
      .catch(err => {
        setSending(false)
        alert(err.message)
      })
  }

  return (
    <div className='ForgotPassword'>
      <Row className='justify-content-center'>
        <SubTitle title={"Forgot Password"} className="m-t-40 w-100" />
        <Col className='d-flex align-items-center auth-bg px-2' lg='5' sm='12'>
          <Col className='px-xl-2 mx-auto' sm='8' md='6' lg='12'>
            <Form className='auth-login-form mt-2' onSubmit={handleSubmit}>
              <FormGroup>
                <Label className='form-label' for='login-email'>
                  Email
                </Label>
                <Input type='email' id='login-email' placeholder='john@example.com' name="email" onChange={e => setEmail(e.target.value)} autoFocus />
              </FormGroup>
              <Button.Ripple type="submit" color='primary' block className="mt-2 flex justify-content-center align-items-center">
                {isSending &&
                  <span className="spinner-border spinner-border-sm mr-1" role="status" aria-hidden="true"></span>
                }
                Submit
              </Button.Ripple>
            </Form>
            <p className='text-center mt-2'>
              <Link to='/signin'>
                <ChevronLeft className='mr-25' size={14} />
                <span className="align-middle">Back to Login</span>
              </Link>
            </p>
          </Col>
        </Col>
      </Row>
    </div>
  )
}

export default ForgotPassword
